import './styles.css';

const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

const bodyRef = document.querySelector('body');
const startBtnRef = document.querySelector('[data-action = start]');
const stopBtnRef = document.querySelector('[data-action= stop]');
// console.log(startBtnRef);
// console.log(stopBtnRef);
let interval;

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};
function startChangingBackgroundColor() {
  interval = setInterval(() => {
    bodyRef.style = `background-color:${
      colors[randomIntegerFromInterval(0, 5)]
    }`;
  }, 1000);
  startBtnRef.disabled = true;
}

function stopChangingBackgroundColor() {
  clearInterval(interval);
  startBtnRef.disabled = false;
}

startBtnRef.addEventListener('click', startChangingBackgroundColor);
stopBtnRef.addEventListener('click', stopChangingBackgroundColor);
